export default class Key extends Component {
    constructor(props){
        super(props);
        this.state = {
            lit : ''
        }
        this.audio = React.createRef();
        this.playAudio = this.playAudio.bind(this);
        this.makeLit = this.makeLit.bind(this);
        this.unLit = this.unLit.bind(this);
    }
    makeLit = () => {
        this.setState({
            lit: 'rgb(240,240,240)'
        })
    }
    unLit = () => {
        this.setState({
                lit: ''
        })
    }
    playAudio = () => {
        this.audio.current.play();
        const parent = this.audio.current.parentNode;
        const display = parent.parentNode.nextSibling.children[0];
        display.querySelector("p").innerText = this.props.id;
    }
    
    render() {
        return (
            <div
            className="drum-pad"
            onMouseDown={this.makeLit}
            onMouseUp={this.unLit}
            onClick={this.playAudio}
            style={{backgroundColor: this.state.lit}}
            // style={{boxShadow: "inset 6px 6px 6px rgb(250,250,230)"}}
            id={"key-" + this.props.text}>
                <p className="each-pad">{this.props.text}</p>
                <audio 
                    ref={this.audio} 
                    src={this.props.audio} 
                    className="clip" 
                    id={this.props.text}/>
            </div> 
        )
    }
}

document.addEventListener('keydown', (e) => {
    const keyStroke = e.key.toUpperCase();
    const audio = document.getElementById(keyStroke);
    if(audio) {
        audio.currentTime = 0;
        const parent = audio.parentNode;
        parent.classList.add("active");

        audio.play();

        audio.addEventListener('ended', () => {
            parent.classList.remove('active');
        })

        const display = parent.parentNode.nextSibling.firstChild;
        const index = keyStroke.toUpperCase();

        sounds.map((elem, idx) => {
            if (index === elem.key){
                display.querySelector('p').innerText = elem.name;
            }
            return true
        })
    }
})